import React, { Component } from 'react';
import { Grid, Row, Col } from 'react-bootstrap';
import { hero } from './Constants';
import Spacing from './Spacing';

class Hero extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Grid fluid={false} className="hero">
                <Row className="text-align-left top-row">
                    <Col md={5} className="height-100">
                        <div className="flex-parent" >
                            <div className="flex-child">
                                <h1 className="margin-bottom-25 header">{hero.header}</h1>
                                <h4 className="margin-bottom-50 body">{hero.body1}<br />{hero.body2}</h4>
                                <button className="cta cta-primary margin-right-40">{hero.button1}</button>
                                {hero.button2 && <button className="cta cta-secondary">{hero.button2}</button>}
                            </div>
                        </div>
                    </Col>
                    <Col md={7} className="height-100">
                        <div className="flex-parent">
                            <div className="flex-child">
                                <img src={hero.src} className="" />
                            </div>
                        </div>
                    </Col>
                </Row>
                <Spacing spacing="medium"></Spacing>
                <Row className="subheader">
                    <Col md={12} className="">
                        <h2 className="margin-bottom-0">{hero.subheader1}</h2>
                        <h2 className="margin-top-0">{hero.subheader2}</h2>
                    </Col>
                </Row>
            </Grid>
           
        )
    }
}

export default Hero;






